.thermometer-screen {
  display: flex;
  justify-content: center;
  width: 130px;
  height: 130px;
  padding-top: 30px;
  text-align: center;
  font-weight: bold;
  font-size: 30px;
  border: 7px solid rgb(203, 191, 191);
  border-radius: 50%;
  color: white;
}

.bg-01 {
  background-color: navy;
}

.bg-02 {
  background-color: #71fa32;
}

.bg-03 {
  background-color: #fab505;
}

.bg-04 {
  background-color: red;
}
